@import url(https://fonts.googleapis.com/css?family=Lato:400,300,300italic,400italic,700,700italic);
* { box-sizing: border-box; }

body {
  margin: 0;
}

h2,h3,h4 {
  margin: 0;
}

.app-header {
  margin: 20px 0px;
  width: 100%;
}
.app-header .title {
  display:inline-block;  
}
.app-header .actions {
  float:right;
}
*:focus {
    outline:none;
}

button {
  padding: 10px 14px;
  background: white;
  border-radius: 30px;
  border: 2px solid #28aadc;
  color: #28aadc;
}
button:hover {
  border-color: #346392;
  color: #346392;
}

.searchBar {
  position: relative;
  margin: 0 auto 20px auto;
  display: block;
  width: 60%;
}

.search {
  border-radius: 50px;
  padding: 10px 15px;
  border: 2px solid #28aadc;
  width: 100%;
}

.searchBar .fa-search {
  position: absolute;
  right: 12px;
  top: 12px;
}

/* Custom CSS for the Rating Widget */
.rating {
  margin: 5px;
  padding: 10px;
  background: rgba(255,255,255, 0.9);
  position: absolute;
  top: 0;
  right: 0;
}
.black {
  color: black;
}
.gold {
  color: #ffc733;
}

/* General CSS for Forms */
.form-row label {
  display:block;
  width: 100%;
  text-align: left;
}
.form-row input[type=text] {
   color:#777;
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0 20px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
}
.form-row input[type=text]:focus {
  outline:none;
  border:1px solid #346392;
}

/* Custom CSS for the react-taginput widget */
.react-tagsinput {
    width: 100%;
    background-color: #fff;
    overflow: hidden;
    padding-left: 5px;
    padding-top: 5px;
    margin: 8px 0 20px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
}
.react-tagsinput--focused {
  border-color: #346392;
}
.react-tagsinput span {
    float:left;    
}

.react-tagsinput-tag {
  background-color: white;
  border-radius: 2px;
  border: 1px solid #28aadc;
  color: #28aadc;
  display: inline-block;
  font-size: 13px;
  font-weight: 700;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 5px;
}
.react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}
.react-tagsinput-tag a::before {
  content: " x";
  color: #28aadc;
}
.react-tagsinput-input {
  background: transparent;
  border: 0;
  color: #777;
  font-weight: 400;
  margin-bottom: 6px;
  margin-top: 1px;
  outline: none;
  padding: 5px;
  width: 80px;
}
/**
*** SIMPLE GRID
*** (C) ZACH COLE 2016
**/

/* UNIVERSAL */

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%;
}

/* ROOT FONT STYLES */

* {
  font-family: 'Lato', Helvetica, sans-serif;
  color: #333447;
  line-height: 1.5;
}

/* TYPOGRAPHY */

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.375rem;
}

h4 {
  font-size: 1.125rem;
}

h5 {
  font-size: 1rem;
}

h6 {
  font-size: 0.875rem;
}

p {
  font-size: 1.125rem;
  font-weight: 200;
  line-height: 1.8;
}

.font-light {
  font-weight: 300;
}

.font-regular {
  font-weight: 400;
}

.font-heavy {
  font-weight: 700;
}

/* POSITIONING */

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.justify {
  text-align: justify;
}

/* ==== GRID SYSTEM ==== */

.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.row {
  position: relative;
  width: 100%;
}

.row [class^="col"] {
  float: left;
  margin: 0.5rem 2%;
  min-height: 0.125rem;
}

.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  width: 96%;
}

.col-1-sm {
  width: 4.33%;
}

.col-2-sm {
  width: 12.66%;
}

.col-3-sm {
  width: 21%;
}

.col-4-sm {
  width: 29.33%;
}

.col-5-sm {
  width: 37.66%;
}

.col-6-sm {
  width: 46%;
}

.col-7-sm {
  width: 54.33%;
}

.col-8-sm {
  width: 62.66%;
}

.col-9-sm {
  width: 71%;
}

.col-10-sm {
  width: 79.33%;
}

.col-11-sm {
  width: 87.66%;
}

.col-12-sm {
  width: 96%;
}

.row::after {
	content: "";
	display: table;
	clear: both;
}

.hidden-sm {
  display: none;
}

@media only screen and (min-width: 33.75em) {  /* 540px */
  .container {
    width: 90%;
  }
}

@media only screen and (min-width: 45em) {  /* 720px */
  .col-1 {
    width: 4.33%;
  }

  .col-2 {
    width: 12.66%;
  }

  .col-3 {
    width: 21%;
  }

  .col-4 {
    width: 29.33%;
  }

  .col-5 {
    width: 37.66%;
  }

  .col-6 {
    width: 46%;
  }

  .col-7 {
    width: 54.33%;
  }

  .col-8 {
    width: 62.66%;
  }

  .col-9 {
    width: 71%;
  }

  .col-10 {
    width: 79.33%;
  }

  .col-11 {
    width: 87.66%;
  }

  .col-12 {
    width: 96%;
  }

  .hidden-sm {
    display: block;
  }
}

@media only screen and (min-width: 60em) { /* 960px */
  .container {
    width: 75%;
    max-width: 60rem;
  }
}

